import { Component, Input, OnInit, EventEmitter, Output, ViewChild, ElementRef, Optional } from '@angular/core';
import { LocaleService } from '@farris/ui-locale';
@Component({
    selector: 'panel-filter-checkbox',
    templateUrl: './checkbox.component.html'
})
/**
 * å«ä¹è¯´æ
* this._item
  Aãcontrolå±æ§çå«ä¹{
    enumValues:æä¸¾é¡¹æ°ç»[{value:æä¸¾é¡¹çå¼,name:æä¸¾é¡¹çæ¾ç¤ºææ¬}],
    isExtend:æ¯å¦æ¾ç¤ºå¨æ©å±é¢æ¿
  Bãvalue å±æ§
     value å¯¹åºå½åå¤éç»ä»¶çå¼
  CãvalueText å±æ§ --å¯¹åºææ¬
}
 */
export class PanelfilterCheckboxComponent implements OnInit {

    checkedAll: boolean = false;
    @Input() enableSearch = false;
    @Input() boolcheck: boolean = false;
    _item: any;
    @Input()
    get item() {
        return this._item;
    }
    set item(data) {
        this._item = data;
        this.getDataSource();
        //this.checkValue = this._item.value.getOriginalValue();
        if (!this._item.hasOwnProperty('value')) this._item.value = { value: [] };
        if (this._item.hasOwnProperty('value')) {
            this.checkValue = this._item.value.value;
            //ä¿å­å¼
            this.allColsCheckValue=this.checkValue;
        }
        else {
            this._item.value.value = [];
        }
        if (this.boolcheck) {
            if (this.checkValue.length) {
                this.boolchecked = this.checkValue[0];
                if (this.boolchecked) this.boolCheckState = 'checked';
                else this.boolCheckState = 'unchecked';
            }
            else this.boolCheckState = 'indeterminate';
        }
        this.enableSearch=data.control.hasOwnProperty("enableSearch")?data.control.enableSearch:false;
        if (this._item.value.value.length == 0) this.checkedAll = false;
        else if (this._item.control['enumValues'] && this._item.value.value.length == this._item.control['enumValues'].length) this.checkedAll = true;
        this.setSubmitChange();
    }

    @ViewChild('searchInput') searchInput: ElementRef;
    @Output('searchHandle') searchHandle = new EventEmitter();
    @Output('submitChange') submitChange = new EventEmitter();
    // è®°å½å¾éçå¼
    checkValue: any[];
    indeterminate: boolean = true;
    boolchecked: boolean;
    boolCheckState: string = 'indeterminate';
    // æ°æ®æº
    checkboxDataSource: Array<any> = new Array<any>();
    // å½åæ¥è¯¢ç»æ
    allCols: Array<any> = new Array<any>();
    // å¨å½åæ¥è¯¢ç»æçéä¸­å¼
    allColsCheckValue:any[]=[];
    //ä¸å¨å½åçæ¥è¯¢ç»æä¸­çéä¸­å¼
    private notInCurResult:any[]=[];
    // æ¯å¦è®¾ç½®æ¥è¯¢æ¶åçå®½åº¦
    private hasSetWidthSearching=false;
    // æ¥è¯¢æç¤º
    searchPlaceHolder="è¯·è¾å¥åç§°";
    checkallCheckboxDisabled=false;
    constructor(@Optional() public localeService: LocaleService)  { }

    ngOnInit(): void {
        this.setLocaleData();
    }

    getDataSource() {
        if (this.item && this._item.control) {
            let checkboxList = (this._item.control);
            this.checkboxDataSource = Array<any>();
            if (checkboxList.enumValues && checkboxList.enumValues.length > 0) {
                checkboxList.enumValues.forEach(element => {
                    this.checkboxDataSource.push(Object.assign({}, element));
                });
                this.allCols = this.checkboxDataSource;
            }
        }
    }

    onSearch(event) {
        let checkboxValue = [];
        if (event) {
            let checkboxList = event;
            if (checkboxList && checkboxList.length) {
                checkboxValue = checkboxList;
            }
            this.allColsCheckValue=checkboxValue;            
        }
        else {
           this.allColsCheckValue=[];
        }
        this.onlyUpdateCheckValue();
        this.searchResultIsCheckAll();
        this.getValueText();
        this.setSubmitChange();
        this.searchHandle.emit();
    }

    getValueText() {
        const enumValue = (this._item.control).enumValues;
        const value = (this._item.value).value;
        if (enumValue && enumValue.length && value && value.length) {
            let texts = [];
            for (let i = 0; i <= value.length; i++) {
                const enums = enumValue.find(val => {
                    return val['value'] == value[i];
                });
                if (enums) {
                    texts.push(enums['name']);
                }
            }
            this._item.valueText = texts.join(',');
        }else{
            this._item.valueText='';
        }
    }
    /**
     * æ¯å¦å¯ä»¥æäº¤
     */
    setSubmitChange() {
        const val = this._item.value;
        if (Object.keys(val).length < 0 || Object.keys(val.value).length === 0) {
            this.submitChange.emit(false);
        }
        else {
            this.submitChange.emit(true);
        }
    }
    /**
     * ç¹å»å¨éCheckbox
     * @param event 
     */
    handleClick(event) {
        if(this.checkallCheckboxDisabled){
        // å¨écheckboxæ¯å¦ç¦ç¨
            return;
        }
        this.checkedAll = !this.checkedAll;
        if (this.checkedAll) {
            // å¨é
            this.setCheckedAll();
        }
        else {
            // åæ¶å¨é
            this.setUnCheckedAll();
        }
        this.getValueText();
        this.setSubmitChange();
        this.searchHandle.emit();
    }
    /**
     * è®¾ç½®å¨é 
     * 23å¹´12æ13æ¥--å¢å è¿æ»¤ï¼æ¹åæ­¤æ¹æ³
     */
    setCheckedAll() {
        if (this.allCols && this.allCols.length) {
            let checkboxValue = [];    
            checkboxValue= this.allCols.map(item=>item.value)    
            this.allColsCheckValue=[].concat(checkboxValue);
            this.onlyUpdateCheckValue();
        }
    }
    /**
     * åæ¶å¨é
     */
    private setUnCheckedAll(){
        if (this.allCols && this.allCols.length) {
             this.allColsCheckValue=[];
             this.onlyUpdateCheckValue();   
        }
    }

    handleCheckClick() {
        if (this.boolCheckState == 'indeterminate') {
            this.boolCheckState = 'checked';
            this.boolchecked = true;
            this._item.value.value = [true];
        }
        else if (this.boolCheckState == 'checked') {
            this.boolCheckState = 'unchecked';
            this.boolchecked = false;
            this._item.value.value = [false];
        }
        else if (this.boolCheckState == 'unchecked') {
            this.boolCheckState = 'indeterminate';
            this.boolchecked = null;
            this._item.value.value = [];
        }
        this.setSubmitChange();
        this.searchHandle.emit();
    }
    /**
     * æ¸ç©ºæ¥è¯¢
     * @param $event 
     * @param input 
     */
    clearSearch($event, input) {
        if (input && input.value) {
            this.filterItems('');
            input.value = '';
        }
    }
    /**
     * å¼å§æ¥è¯¢
     * @param $event 
     */
    searchItems($event) {
        const txt = $event.target.value;
        this.filterItems(txt);
    }
    searchInputFocus(ev){
        if(!this.hasSetWidthSearching){
            let findParEl=this.searchInput.nativeElement.closest('.checkboxgroup-has-search');
            this.hasSetWidthSearching=true;
            findParEl.style.width=findParEl.offsetWidth+'px';
        }
    }
    /**
     * è¿æ»¤é¡¹
     * @param searchWords 
     */
    private filterItems(searchWords = '') {
        let cols = [];
        searchWords=searchWords.trim();
        if (searchWords) {
            let searchArray = searchWords.split(" ");
            let tallColsCheckValue=[];
            this.notInCurResult=[];
            cols = this.checkboxDataSource.filter(item => {
                let waitSearchStr = item.name.toLowerCase();
                let curResult=false;
                // ç©ºæ ¼åä¸ºåä¸ä¸ªå¹éæ¡ä»¶
                 let findNotContain = searchArray.findIndex(searchStr => {
                        // A B ä¸¤ä¸ªæ¡ä»¶è¿æ»¤
                        if (searchStr == '') {
                            return false;
                        }
                        return waitSearchStr.indexOf(searchStr.toLowerCase()) == -1;
                 });
                curResult= findNotContain == -1;
                if(curResult){
                    //å¨æ¥è¯¢ç»æå
                    if(this.checkValue.some((checkItem)=>checkItem==item.value)){
                        //ä¸å¨éæ©çå¼èå´å
                        tallColsCheckValue.push(item.value);
                    }
                }
                return curResult;
            });
            this.allCols = cols;
            this.allColsCheckValue=tallColsCheckValue;
            // ä¸å¨å½åéä¸­çæ°æ®
            this.notInCurResult=this.checkValue.filter(x => tallColsCheckValue.indexOf(x) === -1); 
        } else {
            this.allCols = this.checkboxDataSource;
             //å¦ææ¯æ¥è¯¢åºå¨é¨æ°æ®
             this.allColsCheckValue=[].concat(this.checkValue);
             this.notInCurResult=[];
        }
        // æ´æ¹å¨éç¶æ
        this.searchResultIsCheckAll();
    }
    /**
     * æ¥è¯¢ç»æå¨éçç¶æ
     */
    private searchResultIsCheckAll() {
        // å¦ææ ç»æ
        if (this.allCols.length == 0) {
            this.checkedAll = false;
            this.checkallCheckboxDisabled=true;
        } else {
            if (this.allCols.length > this.allColsCheckValue.length) {
                // æ¥è¯¢ç»æçé¿åº¦æ¯å·²éåç´ å¤
                this.checkedAll = false;
            } else {
                this.checkedAll = true;
            }
            this.checkallCheckboxDisabled=false;
        }
    }
    /**
     * æ´æ°ç¶æ
     */
    private onlyUpdateCheckValue(){        
        this.checkValue=[].concat(this.allColsCheckValue,this.notInCurResult);
        this._item.value.value = this.checkValue;
    }
    private  setLocaleData(){
        if(this.enableSearch&&this.localeService){
            this.searchPlaceHolder= this.localeService.getValue('filterPanel.searchPlaceHolder')||"è¯·è¾å¥åç§°";
         }
    }
}
